:root {
    --tealish: #2ba0aa;
    --off-white: #f6f6f6;
    --white: #ffffff;
    --dark: #282c39;
    --charcoal-grey: #3d414d;
    --charcoal-grey-50: rgba(61, 65, 77, 0.5);
}

.Color-Fill-1-copy-2 {
    background-color: var(--off-white);
    min-width: 1501px;
    min-height: 905px;
    margin-bottom: -70px;
    padding-bottom: 70px;
}

.header-bkgrd {
    width: 1500px;
    height: 68px;
}

.ellipse-2977 {
    width: 43.8px;
    height: 43.8px;
    border-radius: 100%;
    background-color: var(--charcoal-grey);
    margin-top: 5.8px;
    margin-left: 35px;
    color: var(--white);
}

.logo {
    width: 46.9px;
    margin-left: -10.7px;
    padding-top: 15.0px;
    font-size: 13.8px;
}

.logo1 {
    color: var(--charcoal-grey);
}

.headerRight {
    width: 200px;
}

.footer-bkgrd-copy-2 {
    min-width: 1500px;
}

.close-button {
    height: 21.7px;
    width: 21.7px;
}

.not-saved {
    width: 173px;
    height: 12px;
    font-size: 12px;
    text-align: center;
    color: var(--white);
    padding-left: 27.2px;
}

.save-draft {
    width: 137px;
    height: 49px;
    background-color: var(--dark);
    border-radius: 0;
}

.side-menu {
    float: right;
    width: 68px;

}

.side-menu div span {
    width: 68px;
    height: 39px;
    text-align: center;
}

.side-menu div span div {
    font-size: 12px;
    padding: 0;
}

.side-menu div {
    padding: 1px;
}

.records-box {
    width: 317px;
    height: 86.1px;
    border-radius: 10px;
    background-color: var(--off-white);
    border: solid 1px #d7d7d9;
    margin: 1px auto;
}

.record {
    width: 66.1px;
    height: 66.1px;
    border-radius: 10px;
    background-color: var(--white);
    border: solid 1px rgba(40, 44, 57, 0.1);
    display: inline-block;
    text-align: center;
}

.record-number {
    width: 8px;
    height: 14px;
    font-size: 19px;
    font-weight: bold;
    text-align: center;
    color: var(--dark);
}

.record-text {
    width: 34px;
    height: 7px;
    font-size: 9px;
    letter-spacing: 0.1px;
    text-align: center;
    color: var(--dark);
}

.record-container {
    width: 66.1px;
    height: 66.1px;
    border-radius: 10px;
    background-color: #E0E0E0;
    display: inline-block;
    margin: 10px 0 10px 10px;
}

.add-records-box {
    width: 93.1px;
    height: 93.1px;
    border-radius: 10px;
    background-color: var(--white);
    border: solid 2px rgba(61, 65, 77, 0.2);
    margin-left: 120px;
    margin-top: 37.4px;
    text-align: center;
    position: absolute;
    top: 338px;
    left: 507px;
}

.add-records-box-empty p {
    color: var(--charcoal-grey);
}

.add-records-box-added span {
    color: var(--dark);
    font-size: 12px;
}

.pod-circle {
    width: 167.9px;
    height: 167.8px;
    background-color: var(--white);
    border: solid 3px #46c6d1;
    position: absolute;
    top: 336.6px;
    left: 334.7px;
    text-align: center;
}

.lock {
    position: absolute;
    top: 411px;
    left: 554px;
}

.info-box {
    float: right;
    width: 366.5px;
    -webkit-box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.18);
            box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.18);
}

.info-box .title {
    font-size: 25px;
}

.info-box .subtitle {
    font-size: 12px;
    color: rgba(255, 255, 255, 0.5);
    margin-left: 8px;
}

.inner-paper {
    margin: 30px 42px;
}

.inner-paper p {
    font-size: 12px;
}

.pod-circle .pod-name {
    font-size: 36px;
    color: var(--dark);
    font-weight: bold;
}

.pod-circle .pod-state {
    color: var(--charcoal-grey-50);
    opacity: 0.5;
    margin-top: -35px;
}



